body {
  font-family: "Ubuntu", sans-serif;
  color: #252529;
  min-width: 360px; }

a, button {
  cursor: pointer; }

ul, ol {
  list-style-type: none;
  padding: 0;
  margin: 0; }

* {
  box-sizing: border-box; }

.common__radio {
  display: flex;
  align-items: center; }
  .common__radio-wrap {
    display: flex;
    position: relative;
    cursor: pointer; }
  .common__radio-hidden {
    display: none; }
    .common__radio-hidden:checked ~ .common__radio .common__radio-visible {
      border: 1px solid var(--prm); }
      .common__radio-hidden:checked ~ .common__radio .common__radio-visible:after {
        opacity: 1; }
  .common__radio-visible {
    align-items: center;
    border: 1px solid #e4e4e5;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    height: 18px;
    justify-content: center;
    transition: .3s;
    width: 18px;
    margin-right: 10px; }
    .common__radio-visible:after {
      content: '';
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background-color: var(--prm);
      opacity: 0; }

.common__check {
  display: flex;
  align-items: center; }
  .common__check-wrap {
    display: flex;
    position: relative;
    cursor: pointer; }
  .common__check-hidden {
    display: none; }
    .common__check-hidden:checked ~ .common__check .common__check-visible {
      background-color: var(--prm); }
      .common__check-hidden:checked ~ .common__check .common__check-visible:after {
        opacity: 1; }
  .common__check-visible {
    align-items: center;
    border: 1px solid #e4e4e5;
    border-radius: 3px;
    cursor: pointer;
    display: flex;
    height: 18px;
    justify-content: center;
    transition: .3s;
    width: 18px;
    margin-right: 10px; }
    .common__check-visible:after {
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      background-image: url(/assets/images/svg/check.svg); }

.common__color {
  border: 1px solid var(--prm);
  border-radius: 2px;
  background-color: transparent; }

.common__switch {
  width: 70px;
  height: 30px;
  margin: 0 10px;
  position: relative;
  box-shadow: 0px 33px 100px rgba(28, 20, 37, 0.0201946), 0px 7.37098px 22.3363px rgba(28, 20, 37, 0.0298054), 0px 2.19453px 6.6501px rgba(28, 20, 37, 0.05);
  border-radius: 33px;
  background-color: var(--prm);
  order: 2; }
  .common__switch:after {
    content: '';
    position: absolute;
    display: block;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    margin-top: auto;
    margin-bottom: auto;
    background-color: #fff;
    left: 3px;
    right: 0;
    top: 0;
    bottom: 0;
    transition: 0.3s; }
  .common__switch-wrap {
    display: flex;
    align-items: center;
    cursor: pointer; }
  .common__switch-text--uncheck {
    color: var(--prm);
    order: 1; }
  .common__switch-text--check {
    order: 3; }
  .common__switch-hidden {
    display: none; }
    .common__switch-hidden:checked ~ .common__switch:after {
      transform: translateX(38px); }
    .common__switch-hidden:checked ~ .common__switch-text--uncheck {
      color: #252529; }
    .common__switch-hidden:checked ~ .common__switch-text--check {
      color: var(--prm); }

.btn {
  color: #fff;
  background-color: var(--prm);
  border: none;
  border-radius: 33px;
  height: 40px;
  font-weight: 700;
  font-size: 14px;
  line-height: 130%;
  transition: 0.3s;
  width: 200px;
  margin: auto; }
  .btn:hover {
    opacity: .7; }
  .btn__wrap {
    display: flex;
    width: 100%;
    justify-content: center; }
  .btn__open {
    position: absolute;
    top: 10px;
    left: 10px;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    background-color: #F7F5FF;
    transform-origin: center;
    transition: opacity 0.3s; }
    .btn__open:hover {
      animation: spin 4s linear infinite; }
    .btn__open-inner {
      width: 50px;
      height: 50px; }
    .btn__open svg {
      display: block;
      width: 100%;
      height: 100%; }
      .btn__open svg path {
        fill: var(--prm); }
    .btn__open.hide {
      opacity: 0; }
  .btn__close {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: var(--prm);
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    transition: opacity 0.3s; }
    .btn__close:hover {
      opacity: .5; }

@keyframes spin {
  100% {
    transform: rotate(360deg); } }

.panel {
  position: fixed;
  top: 0;
  left: 0; }
  .panel__inner {
    position: relative;
    width: 100%;
    max-width: 400px;
    background: #FFFFFF;
    box-shadow: 0px 15px 125px rgba(201, 180, 250, 0.13), 0px 4.52206px 37.6838px rgba(201, 180, 250, 0.0847066), 0px 1.87823px 15.6519px rgba(201, 180, 250, 0.065), 0px 0.67932px 5.661px rgba(201, 180, 250, 0.0452935);
    border-radius: 16px;
    padding: 20px;
    transition: transform 0.3s;
    transform: translateX(-100%); }
  .panel__header {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center; }
  .panel__title {
    font-size: 20px;
    line-height: 24px;
    font-weight: 700;
    display: block; }
  .panel__subtitle {
    font-size: 16px;
    line-height: 18px;
    display: block;
    margin-bottom: 10px; }
  .panel__section {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    margin-bottom: 16px; }
    .panel__section:last-child {
      margin-bottom: 0; }
    .panel__section-item--part {
      width: 100%;
      max-width: 47%; }
  .panel__part {
    padding: 20px 0;
    border-bottom: 1px solid #F7F5FF; }
    .panel__part:last-child {
      border-bottom: none; }
  .panel.active .panel__inner {
    transform: translateX(0);
    z-index: 2; }

.alert {
  width: 120px;
  text-align: center;
  position: absolute;
  font-size: 12px;
  background-color: var(--prm);
  color: #fff;
  padding: 10px;
  border-radius: 3px;
  top: 15px;
  right: 15px;
  opacity: 0;
  z-index: 1;
  pointer-events: none;
  transition: .5s; }
  .alert.active {
    opacity: .5; }
